import useGtm from 'hooks/useGtm';


window.addEventListener('load', function () {
    // obtain cookieconsent plugin
    var cookieconsent = window.initCookieConsent();
    // run plugin with config object
    cookieconsent.run({
        autorun: true,
        current_lang: 'en',
        autoclear_cookies: true,
        page_scripts: true,
        force_consent: true,
        onFirstAction: function(user_preferences){
          let consentChoice = useGtm().triggerConsentChange(user_preferences);
          window.isAnalyticsEnabled = true;
          setTimeout(() => {
            const gtm = useGtm().getGtm();
            gtm.enable(false);
            if (consentChoice === 'optin') {
                gtm.enable(true);
                useGtm().triggerPageView(window.pageViewRoute);
                window.retryEvents.forEach(function callback(value, index) {
                    if (value.type === 'product') {
                        useGtm().triggerProductPageItem(value.params[0], value.params[1], value.params[2]);
                    } else if (value.type === 'cart') {
                        useGtm().triggerCartView(value.params[0], value.params[1])
                    } else if (value.type === 'checkout') {
                        useGtm().triggerCartView(value.params[0], value.params[1], value.params[2])
                    }
                    window.retryEvents.splice(index, 1);
                });
            }
          }, 250);
            // callback triggered only once
        },
  
        languages: {
            en: {
                consent_modal: {
                    title: 'I use cookies',
                    description: 'Hi, this website uses essential cookies to ensure its proper operation and tracking cookies to understand how you interact with it. The latter will be set only upon approval.',
                    primary_btn: {
                        text: 'Accept',
                        role: 'accept_all'              // 'accept_selected' or 'accept_all'
                    },
                    secondary_btn: {
                        text: 'Settings',
                        role: 'settings'                // 'settings' or 'accept_necessary'
                    }
                },
                settings_modal: {
                    title: 'Cookie preferences',
                    save_settings_btn: 'Save settings',
                    accept_all_btn: 'Accept all',
                    reject_all_btn: 'Reject all',       // optional, [v.2.5.0 +]
                    cookie_table_headers: [
                        {col1: 'Name'},
                        {col2: 'Domain'},
                        {col3: 'Expiration'},
                        {col4: 'Description'},
                        {col5: 'Type'}
                    ],
                    blocks: [
                        {
                            title: 'Cookie usage',
                            description: 'I use cookies to ensure the basic functionalities of the website and to enhance your online experience. You can choose for each category to opt-in/out whenever you want.'
                        }, {
                            title: 'Strictly necessary cookies',
                            description: 'These cookies are essential for the proper functioning of my website. Without these cookies, the website would not work properly.',
                            toggle: {
                                value: 'necessary',
                                enabled: true,
                                readonly: true
                            }
                        }, {
                            title: 'Analytics cookies',
                            description: 'These cookies collect information about how you use the website, which pages you visited and which links you clicked on. All of the data is anonymized and cannot be used to identify you.',
                            toggle: {
                                value: 'analytics',
                                enabled: false,
                                readonly: false
                            },
                            cookie_table: [
                                {
                                    col1: '^_ga',
                                    col2: 'google.com',
                                    col3: '2 years',
                                    col4: 'description ...',
                                    col5: 'Permanent cookie',
                                    is_regex: true
                                },
                                {
                                    col1: '_gid',
                                    col2: 'google.com',
                                    col3: '1 day',
                                    col4: 'description ...',
                                    col5: 'Permanent cookie'
                                }
                            ]
                        }, {
                            title: 'More information',
                            description: 'For any queries in relation to my policy on cookies and your choices, please contact our team.',
                        }
                    ]
                }
            }
        }
    });
  });