import { useI18n } from 'vue-i18n';
import Spinner from 'presentation/components/Spinner/Spinner.vue';
import CartLikeContentDetail from './CartLikeContentDetail/CartLikeContentDetail.vue';
import AddDiscountCodeForm from './AddDiscountCodeForm/AddDiscountCodeForm.vue';
import CartLikePriceDetail from './CartLikePriceDetail/CartLikePriceDetail.vue';
import useCartTools from 'hooks/useCartTools';
import useCart from 'hooks/useCart';
import { onUpdated, ref } from 'vue';
import useGtm from 'hooks/useGtm';
import { useRoute } from 'vue-router';

// import AddDiscountCodeForm from '../AddDiscountCodeForm/AddDiscountCodeForm.vue';
export default {
  name: 'CartDetail',
  components: {
    Spinner,
    CartLikeContentDetail,
    AddDiscountCodeForm,
    CartLikePriceDetail,
  },
  setup() {
    const { t } = useI18n();
    const { cart, loading, error } = useCart();
    const already = ref(null);
    onUpdated(() => {
      const route = useRoute();
      if (!already.value && cart.value) { 
        useGtm().triggerCartView(cart.value, route);
        if (!useGtm().getGtm().enabled()) {
          let call = {
            'type' : 'cart',
            'params': [
              cart.value, route
            ]
          };
          window.retryEvents.push(call);
        }
        already.value = true;
      }
    });
    return {
      t,
      cart,
      loading,
      error,
      ...useCartTools(),
    };
  },
};
