import { onUpdated, ref } from 'vue';
import useProductTools from 'hooks/useProductTools';
import ProductInfo from './ProductInfo/ProductInfo.vue';
import useGtm from 'hooks/useGtm';
import { useRoute } from 'vue-router';

export default {
  name: 'PageProductDetail',
  setup() {
    const { allVariants, currentVariant, sku, error, product } =
      useProductTools(true);
    const showAddToShoppingList = ref(false);
    const productSku = ref(null);
    const openAddToShoppingList = () => {
      showAddToShoppingList.value = true;
    };
    const closeAddToShoppingList = () => {
      showAddToShoppingList.value = false;
    };
    const previousItemId = ref(null);
    onUpdated(() => {
      const route = useRoute();
      if (currentVariant.value) {
        if (currentVariant.value?.sku != previousItemId.value) {
          useGtm().triggerProductPageItem(currentVariant.value, product.value, route);
          if (!useGtm().getGtm().enabled()) {
            let call = {
              'type' : 'product',
              'params': [
                currentVariant.value, product.value, route
              ]
            };
            window.retryEvents.push(call);
          }
          previousItemId.value = currentVariant.value?.sku;
        }
      }
    });
    return {
      openAddToShoppingList,
      closeAddToShoppingList,
      productSku,
      allVariants,
      currentVariant,
      error,
      sku,
      product
    };
  },
  components: {
    // Breadcrumb,
    ProductInfo,
    // AddToShoppingList
  },
};
