import { VueperSlides, VueperSlide } from 'vueperslides';
import 'vueperslides/dist/vueperslides.css';
import { useI18n } from 'vue-i18n';
import config from '../../../../sunrise.config';
import getter from '../../../../composition/getCookie';

export default {
  setup() {
    const { t } = useI18n({
      inheritLocale: true,
      useScope: 'local',
    });
    return {
      t,
    };
  },
  components: {
    VueperSlides,
    VueperSlide,
  },
  data: () => ({
    autoPlaying: true,
    internalAutoPlaying: true,
    slides: [],
  }),
  mounted () {
    fetch(config.zeotap.bannerHost + '/get_banner' + '?cookieID=' + getter().getCdpCookie())
      .then(res => res.json())
      .then (data => this.slides = data.data.banners)
      .catch((err) => { 
        let data = require('../../../../data/db.json')
        this.slides = data.banners.data.banners;
        console.log(err.message)
        console.log(this.slides)
    });
  }
};
