// import ProductFilter from '../ProductFilter/ProductFilter.vue';
// import TopBar from '../TopBar/TopBar.vue';
import Pagination from 'presentation/components/Pagination/Pagination.vue';
import Spinner from 'presentation/components/Spinner/Spinner.vue';
import ProductThumbnail from './ProductThumbnail/ProductThumbnail.vue';
import { useI18n } from 'vue-i18n';
import useProductTools from 'hooks/useProductTools';
import useCartTools from 'hooks/useCartTools';
import useGtm from 'hooks/useGtm';
import { onBeforeRouteLeave, useRoute } from "vue-router";

export default {
  name: 'ProductList',
  components: {
    Spinner,
    ProductThumbnail,
    Pagination,
    // ProductFilter,
    // TopBar,
  },
  
  setup() {
    const { t } = useI18n();
    const { addLine } = useCartTools();
    const route = useRoute();
    const {
      formatProduct,
      products,
      total,
      loading,
      page,
      error,
      setPage,
    } = useProductTools(true);
    const addToCart = (sku, quantity = 1) => {
      addLine(sku, quantity),
      products.value.forEach(element => {
        if (element?.masterVariant?.sku === sku) {
          useGtm().pushToStorage(element);
          useGtm().triggerItem(element, 'add_to_cart', route);
        }
      });
    }
    // This needs to be defined outside the setup block
    onBeforeRouteLeave((to, from, next) => {
      if (to.name === 'product') {
        var product = products.value.filter(function(elem){
          if(elem.slug === to.params.productSlug) return elem;
        });
        useGtm().triggerItem(product, 'select_item', from);
      }
      next()
    });
    return {
      t,
      formatProduct,
      products,
      total,
      loading,
      page,
      setPage,
      error,
      addToCart,
    };
  },
};
