import { shallowRef, watch } from 'vue';
import useCartTools from 'hooks/useCartTools';
import { useRoute } from 'vue-router';
import useGtm from 'hooks/useGtm';

export default {
  name: 'LineItemQuantityForm',
  components: {
    // BaseForm,
    // BaseInput,
  },
  props: {
    lineItemId: {
      type: String,
      required: true,
    },
    quantity: {
      type: Number,
      required: false,
    },
    lineItem: {
      type: Object,
      required: false,
    },
  },
  setup(props) {
    const route = useRoute();
    const { changeLine: cl, removeLineItem: rm } =
      useCartTools();
    const quantity_ = shallowRef(props.quantity);
    const changeLine = () => {
      let quantityDiff = props.lineItem.quantity - quantity_.value;
      if (props.lineItem.quantity > quantity_.value) {
        useGtm().triggerRemoveItem(props.lineItem, route, quantityDiff);
      } else {
        useGtm().triggerRemoveItem(props.lineItem, route, Math.abs(quantityDiff), 'add_to_cart');
      }
      cl(props.lineItemId, quantity_.value);
    }
    const removeLineItem = (trigger = true) => {
      rm(props.lineItemId);
      if (trigger) {
        useGtm().triggerRemoveItem(props.lineItem, route);
      }
    }
    watch(quantity_, (q) => {
      if (q === '') {
        return;
      }
      changeLine(q);
      if (q <= 0) {
        removeLineItem(false);
      }
    });
    const changeLineItemQuantity = () => {
      return changeLine(quantity_.value);
    };
    const increment = () => {
      quantity_.value += 1;
    };
    const decrement = () => {
      quantity_.value -= 1;
    };

    return {
      quantity_,
      increment,
      decrement,
      removeLineItem,
      changeLineItemQuantity,
    };
  },
};
