import {
  computed,
  onMounted,
  onUnmounted,
  provide,
  shallowRef,
  watch,
} from 'vue';
import { LOCALE } from '../../../../constants';
import { LOCATION } from '../../../../constants';
import { getValue } from '../../../../lib';
import i18n from '../../../../i18n';
import useMiniCart from 'hooks/useMinicart';
import MiniCart from 'presentation/Header/MiniCart/MiniCart.vue';
import useDefaultRouteParams from 'hooks/useDefaultRouteParams';
import useGtm from 'hooks/useGtm';
import './cookie.js';

export default {
  components: {
    MiniCart,
  },
  watch: {
    $route(to, from) {
       if (
        (typeof to.params.country === "undefined" || ((to.params.country == "") && (to.params.locale == "")))
        && (from.fullPath !== ('/' + from.params.country + '/' + from.params.locale + ((to.fullPath !== '/') ? to.fullPath : '')))
        ) {
          useGtm().triggerPageView(to);
       }
    }
  },
  setup() {
    const { locale, location, setLocale, setLocation } =
      useDefaultRouteParams();
    const { close, isOpen } = useMiniCart();
    const keyUpListener = shallowRef((e) => {
      if (e.key === 'Escape') {
        close();
      }
    });
    provide(LOCALE, { locale, setLocale });
    provide(LOCATION, { location, setLocation });
    const paramsSet = computed(
      () => getValue(locale) && getValue(location)
    );
    watch(paramsSet, (set) => {
      if (set) {
        i18n.global.locale = locale;
      }
    });
    onMounted(() => {
      useGtm().triggerPageView();
      document.body.addEventListener(
        'keyup',
        keyUpListener.value
      );
    });
    onUnmounted(() => {
      document.body.removeEventListener(
        'keyup',
        keyUpListener.value
      );
    });
    return { paramsSet, isOpen, close };
  },
};
