const getter = () => { 
    const getCdpCookie = () => {
        // document.cookie = "zpstorage_MDU4YzhlOWYtMTgxYy00MDcwLWI1M2MtMzQyMzZlYmE4NDcxzizeotapcdpdemo.github.io=ImI1NjNkM2I3LTQ0MjctNDVmOS1iNmE0LWJjMzFkMjVkYWYzYyI%3D";
        // document.cookie = "zpstorage_MDU4YzhlOWYtMTgxYy00MDcwLWI1M2MtMzQyMzZlYmE4NDcxzszeotapcdpdemo.github.io=ImI1NjNkM2I3LTQ0MjctNDVmOS1iNmE0LWJjMzFkMjVkYWYzYyI%3D";
        let cookie = Object.fromEntries(document.cookie.split('; ').map(v=>v.split(/=(.*)/s)));
        let zi = Object.keys(cookie).filter(e => e.includes("zpstorage")).find(e => e.includes("cxzi"));
        if (zi) {
            return window.atob(cookie[zi].replace("%3D", "")).split('"').join('');
        }
        return 'no-cookie-found';
    }
    return { getCdpCookie };
}

export default getter;