import { useGtm } from '@gtm-support/vue-gtm';
import { useRoute } from 'vue-router';
import useCustomerTools from 'hooks/useCustomerTools';

const findItemAttribute = (attributes, attributeName, defaultValue = 'Not Available') => {
    var attribute = attributes.filter(function(elem){
        if(elem.name === attributeName) return elem;
      });
    return (attribute.length > 0) ? attribute[0]?.value?.label : defaultValue;
}

const findItemVariant = (variant, from) => {
    let itemVariant = findItemAttribute(variant?.attributesRaw, 'color');
    if (itemVariant && typeof itemVariant === 'object' && !Array.isArray(itemVariant)) {
        let locale = from.params.locale;
        itemVariant = itemVariant[locale];
    }
    return itemVariant;
}

const  appendCategoryToItem = (item, product) => {
     // add categories
     let categories = [];
     let categorySlug = '';
     product.categories.forEach(element => {
        categories.push(element.name);
        if (element?.ancestors.length > 0) {
            element.ancestors.forEach(innerElement => {
                categories.push(innerElement.name);
            });
        }
        if (Object.hasOwn(element, 'slug') && (!element.slug.includes('sale') && !element.slug.includes('new'))) {
            categorySlug = element.slug;
        }
     });
     let unique = (a,t={}) => a.filter(e=>!(t[e]=e in t));
     categories = unique(categories);
     for (let index = 0; index < 5; index++) {
         const element = categories[index];
         let indexLabel = (index === 0) ? '' : (index + 1);
         if (element) {
             item['item_category' + indexLabel] = element;
         }
     }
     item['item_category_hierarchy'] = categorySlug;
     return item;
}

const trackEvent = (item, eventType) => {
    const gtm = useGtm();
    let trackData = {
        ecommerce: null
    }
    // Clear the previous ecommerce object
    gtm.trackEvent(trackData);

    let items = [item];
    trackData = {
        event: eventType,
        ecommerce: {
            items: items
        }
    };
    gtm.trackEvent(trackData);
}

const fetchMainProduct = sku => {
    let mainProduct = null;
    let productsInStorage = localStorage.getItem('productsInStorage') ? JSON.parse(localStorage.getItem('productsInStorage')) : [];
    for (const [key, innerElement] of Object.entries(productsInStorage)) {
        if (innerElement.masterVariant.sku === sku) {
            mainProduct = productsInStorage[key];
            break;
        } else {
            innerElement.variants.forEach( element => {
                if (element.sku === sku) {
                    mainProduct = productsInStorage[key];
                }
            });
        }
    }
    return mainProduct;
}

export default () => {
    const triggerPageView = ($route) => {
        const tools = useCustomerTools();
        const gtm = useGtm();
        const route = useRoute();
        if (!window.isAnalyticsEnabled) {
            window.pageViewRoute = route;
        }
        let pageCategory = mapPageCategory((typeof route !== "undefined") ? route.name : $route.name);
        function mapPageCategory(pageCategory) {
            switch (pageCategory) {
                case 'products':
                    return 'product-listing'        
                default:
                    return pageCategory;
            }
        }
        let trackData = {
            event: 'page_view',
            loggedin: false,
            page_url: window.location.href,
            page_category: pageCategory,
        };
        if (tools.customer.value) {
            trackData.loggedin = true;
            trackData.email = tools.customer.value.email;
            trackData.name = tools.customer.value.firstName + " " + tools.customer.value.lastName;
        }
        gtm.trackEvent(trackData);
    }

    const triggerItem = (product, eventType, from) => {
        if (Array.isArray(product)) {
            product = product.pop();
        }
        let itemVariant = findItemVariant(product.masterVariant, from);

        let item = {
            item_id: product?.masterVariant?.sku,
            item_name: product?.name,
            currency: product?.masterVariant?.scopedPrice?.value?.currencyCode,
            index: 0,
            item_brand: findItemAttribute(product.masterVariant.attributesRaw, 'designer'),
            item_list_name: new URL(window.location.href).pathname.split('/').filter(Boolean).pop(),
            item_variant: itemVariant,
            price: (product?.masterVariant?.scopedPrice?.value?.centAmount) / 100,
            quantity: 1
        }
        item = appendCategoryToItem(item, product);
        trackEvent(item, eventType);
    }

    const triggerProductPageItem = (currentVariant, product, route, quantity = 1, eventType = 'view_item') => {
        let itemVariant = findItemVariant(product.masterVariant, route);
        let item = {
            item_id: currentVariant?.sku,
            item_name: product?.name,
            currency: currentVariant?.scopedPrice?.value?.currencyCode,
            index: 0,
            item_brand: findItemAttribute(product.masterVariant.attributesRaw, 'designer'),
            item_variant: itemVariant,
            price: (currentVariant?.scopedPrice?.value?.centAmount) / 100,
            quantity: quantity
        }
        item = appendCategoryToItem(item, product);
        trackEvent(item, eventType);
    };

    const triggerRemoveItem = (product, route, quantity = null, eventType = 'remove_from_cart') => {
        let itemVariant = findItemVariant(product.variant, route);
        let item = {
            item_id: product.variant?.sku,
            item_name: product?.name,
            currency: product?.totalPrice?.currencyCode,
            index: 0,
            item_brand: findItemAttribute(product.variant.attributesRaw, 'designer'),
            item_variant: itemVariant,
            price: ((product?.totalPrice?.centAmount) / 100 / product.quantity),
            quantity: (quantity ? quantity : product.quantity)
        }
        // search for categories in local storage
        let mainProduct = fetchMainProduct(product.variant?.sku);
        item = appendCategoryToItem(item, mainProduct);
        trackEvent(item, eventType);
    };

    const triggerCartView = (cart, route, eventType = 'view_cart') => {
        const gtm = useGtm();
        let trackData = {
            ecommerce: null
        }
        // Clear the previous ecommerce object
        gtm.trackEvent(trackData);
        let items = [];
        cart.lineItems.forEach((product, index) => {
            let itemVariant = findItemVariant(product.variant, route);
            let item = {
                item_id: product.variant?.sku,
                item_name: product?.name,
                currency: product?.totalPrice?.currencyCode,
                index: index,
                item_brand: findItemAttribute(product.variant.attributesRaw, 'designer'),
                item_variant: itemVariant,
                price: ((product?.totalPrice?.centAmount) / 100 / product.quantity),
                quantity: product.quantity
            }
            // search for categories in local storage
            let mainProduct = fetchMainProduct(product.variant?.sku);
            item = appendCategoryToItem(item, mainProduct);
            items.push(item);
        });
            
        trackData = {
            event: eventType,
            ecommerce: {
                items: items
            }
        };

        if (eventType === 'view_cart' || eventType === 'purchase') {
            trackData.ecommerce.currency = cart.totalPrice.currencyCode;
            trackData.ecommerce.value = cart.totalPrice.centAmount / 100;
        }
        if (eventType === 'purchase') {
            trackData.ecommerce.transaction_id = localStorage.getItem('orderId') ? localStorage.getItem('orderId') : 'Not Available';
        }
        gtm.trackEvent(trackData);
    }

    const pushToStorage = (element) => {
        let productsInStorage = localStorage.getItem('productsInStorage') ? JSON.parse(localStorage.getItem('productsInStorage')) : [];
        let productFound = false;
        productsInStorage.forEach(innerElement => {
          if (innerElement.productId == element.productId) {
            productFound = true;
          }
        });
        if (!productFound) {
          productsInStorage.push(element);
        }
        localStorage.setItem('productsInStorage', JSON.stringify(productsInStorage));
    }

    const triggerConsentChange = (user_preferences) => {
        const gtm = useGtm();
        const consentChoice = (user_preferences.accept_type === 'all' ? 'optin' : 'optout');
        let trackData = {
            event: 'consent',
            consent_choice: consentChoice,
        };
        gtm.enable(true);
        gtm.trackEvent(trackData);
        return consentChoice;
    }

    const getGtm = () => {
        return useGtm();
    }

    return {triggerPageView, triggerItem, triggerProductPageItem, triggerRemoveItem, pushToStorage, triggerCartView, triggerConsentChange, getGtm}
};
